import { useState, useEffect, useMemo } from "react";
export function useStateNode(stateNode) {
    const [value, setValue] = useState(stateNode.get());
    useEffect(() => stateNode.subscribe(setValue), [stateNode]);
    return useMemo(() => {
        return new Proxy(stateNode, {
            get: (self, property) => {
                if (typeof property !== "string")
                    return undefined;
                if (property === "value")
                    return value;
                if (["get", "subscribe"].includes(property))
                    return undefined;
                return self[property];
            }
        });
    }, [stateNode, value]);
}
